import React, { useState } from "react";
import { dbService, storageService } from "fbase";
import { limitTitle } from "Functions";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash, faPencilAlt, faWonSign, faMapMarkerAlt, faHome, faStore, faSnowplow, faIndustry, faHouseUser, faBuilding, faPhoneSquareAlt, faObjectGroup, faLayerGroup } from "@fortawesome/free-solid-svg-icons";
import soldPic from '../images/sold.png';
import './SearchCard.css';

const SearchCard = ({ productObj }) => {
    const [editing, setEditing] = useState(false);
    const [newNumber, setNewNumber] = useState(productObj.number);
    const [newProduct, setNewProduct] = useState(productObj.title);
    const [newContent, setNewContent] = useState(productObj.content);
    const [newPrice, setNewPrice] = useState(productObj.price);
    const [newPriceRange, setNewPriceRange] = useState(productObj.priceRange);
    const [newRegion, setNewRegion] = useState(productObj.region);
    const [newType, setNewType] = useState(productObj.type);
    const [newSize, setNewSize] = useState(productObj.size);
    const [newStructure, setNewStructure] = useState(productObj.structure);
    const [newSold, setNewSold] = useState(productObj.sold);
    const onDeleteClick = async () => {
        const ok = window.confirm("ÏÇ≠Ï†ú ÌïòÏãúÍ≤†ÏäµÎãàÍπå?");
        if (ok) {
            await dbService.doc(`products/${productObj.id}`).delete();
            await storageService.refFromURL(productObj.attachmentUrl).delete();
        }
    };
    const toggleEditing = () => {
        setNewNumber(newNumber);
        setNewProduct(newProduct);
        setNewContent(newContent);
        setNewPrice(newPrice);
        setNewPriceRange(newPriceRange);
        setNewRegion(newRegion);
        setNewType(newType);
        setNewSize(newSize);
        setNewStructure(newStructure);
        setNewSold(newSold)
        setEditing((prev) => !prev);
    };
    const onSubmit = async (event) => {
        event.preventDefault();
        await dbService
                .doc(`products/${productObj.id}`)
                .update({
                    number: newNumber,
                    title: newProduct,
                    content: newContent,
                    price: newPrice,
                    priceRange: newPriceRange,
                    region: newRegion,
                    type: newType,
                    size: newSize,
                    structure: newStructure,
                    sold: newSold,
                });
        setEditing(false);
    };
    const onChange = (event) => {
        let {
            target: { name, value },
        } = event;
        if (name === "number") {
            setNewNumber(value);
        } else if (name === "title") {
            setNewProduct(value);
        } else if (name === "content") {
            setNewContent(value);
        } else if (name === "price") {
            setNewPrice(value);
        } else if (name === "priceRange") {
            setNewPriceRange(Number(value));
        } else if (name === "type") {
            setNewType(value);
        } else if (name === "region") {
            setNewRegion(value);
        } else if (name === "size") {
            setNewSize(value);
        } else if (name === "structure") {
            setNewStructure(value);
        } else if (name === "sold") {
            setNewSold(value);
        };
    };
    return (
        <div className="search-card-item col-lg-4 col-md-6">
            { editing ? (
            <>
            <div className="search-card">
            <form onSubmit={onSubmit} className="col-lg-12 product-recom-container manage-form">
                <input
                type="text"
                placeholder="Îß§Î¨º Î≤àÌò∏Î•º ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newNumber}
                name="number"            
                autoFocus
                onChange={onChange}
                className="formInput"
                />
                <input
                type="text"
                placeholder="Ï†úÎ™©ÏùÑ ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newProduct}
                name="title"            
                onChange={onChange}
                className="formInput"
                />
                <textarea
                rows="5" cols="20"
                name="content"            
                placeholder="ÏÑ§Î™ÖÏùÑ ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newContent}
                onChange={onChange}
                className="formInput formContent"
                />
                <input
                type="text"
                name="price"            
                placeholder="Í∏àÏï°ÏùÑ ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newPrice}
                onChange={onChange}
                className="formInput"
                />
                <input
                type="number"
                step="0.01"
                min="0"
                name="priceRange"            
                placeholder="Ïòà) 0.0"
                value={newPriceRange}
                onChange={onChange}
                className="formInput"
                />
                <select value={newType} onChange={onChange} name="type" id="type" className="formInput">
                    <option value="">Îß§Î¨º Ï¢ÖÎ•ò ÏàòÏ†ï</option>
                    <option value="Ï£ºÌÉù">Ï£ºÌÉù</option>
                    <option value="ÏÉÅÍ∞ÄÍ±¥Î¨º">ÏÉÅÍ∞ÄÍ±¥Î¨º</option>
                    <option value="ÌÜ†ÏßÄ">ÌÜ†ÏßÄ</option>
                    <option value="Í≥µÏû•Ï∞ΩÍ≥†">Í≥µÏû• ¬∑ Ï∞ΩÍ≥†</option>
                    <option value="Ï†ÑÏõêÏ£ºÌÉù">Ï†ÑÏõêÏ£ºÌÉù</option>     
                    <option value="ÏïÑÌååÌä∏">ÏïÑÌååÌä∏</option>     
                </select>
                <input
                type="text"
                name="region"            
                placeholder="ÏßÄÏó≠ÏùÑ ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newRegion}
                onChange={onChange}
                className="formInput"
                />
                <textarea
                type="text"
                name="size"            
                placeholder="Î©¥Ï†ÅÏùÑ ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newSize}
                onChange={onChange}
                className="formInput"
                />
                <input
                type="text"
                name="structure"            
                placeholder="Íµ¨Ï°∞Î•º ÏàòÏ†ï ÌïòÏÑ∏Ïöî."
                value={newStructure}
                onChange={onChange}
                className="formInput"
                />  
                <select value={newSold} onChange={onChange} name="sold" id="sold" className="formInput">
                    <option value="">Í≥ÑÏïΩ Ïó¨Î∂Ä ÏàòÏ†ï</option>
                    <option value="ÎØ∏ÏôÑÎ£å">ÎØ∏ÏôÑÎ£å</option>
                    <option value="ÏôÑÎ£å">ÏôÑÎ£å</option>
                </select>          
                <div>
                    <input type="submit" value="ÏàòÏ†ï ÏôÑÎ£å" className="formBtn" />
                    <p onClick={toggleEditing} className="formBtn cancelBtn">
                        Ï∑®ÏÜå
                    </p>         
                </div>  
            </form>
        </div>
        </>            
        ) : (
        <> 
        <div className="product-recom-container container row">
            <div data-aos="fade-up" key={productObj.id} className="product-recom-item">
                <h4><span className="product-id">Îß§Î¨ºÎ≤àÌò∏-[{productObj.number}]</span>{limitTitle(productObj.title)}</h4>
                <span className="product-action">Ï∂îÏ≤ú</span>
                { productObj.sold === "ÏôÑÎ£å" && <img src={soldPic} className="product-sold" alt="sold" />}
                <img src={productObj.attachmentUrl} alt="product-pic"/>
                <div className="product-recom-list">
                    <p className="product-won"><FontAwesomeIcon icon={faWonSign} /> {productObj.price}</p>
                    <p className="product-location"><FontAwesomeIcon icon={faMapMarkerAlt} />{productObj.region}</p>
                    <div className="product-detail">
                        <p className="col-xs-12 row">
                            <span className="col-xs-6">
                                { productObj.type === "Ï£ºÌÉù"
                                ? <FontAwesomeIcon icon={faHome} /> 
                                : productObj.type === "ÏÉÅÍ∞ÄÍ±¥Î¨º"
                                ? <FontAwesomeIcon icon={faStore} /> 
                                : productObj.type === "ÌÜ†ÏßÄ"
                                ? <FontAwesomeIcon icon={faSnowplow} />    
                                : productObj.type === "Í≥µÏû•Ï∞ΩÍ≥†"
                                ? <FontAwesomeIcon icon={faIndustry} />    
                                : productObj.type === "Ï†ÑÏõêÏ£ºÌÉù"
                                ? <FontAwesomeIcon icon={faHouseUser} />    
                                : productObj.type === "ÏïÑÌååÌä∏"
                                ? <FontAwesomeIcon icon={faBuilding} /> 
                                : "Error"                                                                                
                            } {productObj.type}</span>
                            <span className="col-xs-6">
                                { productObj.structure === "Î¨∏Ïùò"
                                ? <FontAwesomeIcon icon={faPhoneSquareAlt} />
                                : <FontAwesomeIcon icon={faObjectGroup} />} {productObj.structure}
                        </span>
                        </p>
                        <p className="upload-size row">
                            <span className="size-icon"><FontAwesomeIcon icon={faLayerGroup} /></span>
                            <span className="size-text"> {productObj.size}</span>
                        </p>              
                    </div>
                </div>
                <div className="product__actions">
                    <span onClick={onDeleteClick}>
                        <FontAwesomeIcon icon={faTrash} />
                    </span>
                    <span onClick={toggleEditing}>
                        <FontAwesomeIcon icon={faPencilAlt} />
                    </span>
                </div>        
            </div>
        </div>    
        </>
        )}
    </div>
    );
};

export default SearchCard;
